<template>
  <div class="equipment-affiliation">
    <box>
      <!--  start -->
      <div class="search">
        <div class="search-body">
          <a-form layout="inline" class="search-main">
            <a-form-item label="">
              <a-select
              class="default-select"
                dropdown-class-name="default-select-dropdown"
                placeholder=""
              >
                <a-select-option value="jack">
                  1
                </a-select-option>
                <a-select-option value="lucy">
                  2
                </a-select-option>
              </a-select>
            </a-form-item>
            <a-form-model-item label="" prop="startTime">
              <a-date-picker
                class="default-date-picker"
                show-time
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
            <a-form-model-item label="" prop="endTime">
              <a-date-picker
                class="default-date-picker"
                show-time
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
          </a-form>
        </div>
      </div>
      <!--  end -->

      <!--  start -->
      <div class="operation-nav">
        <div class="btn-box">
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-sousuo1"></span>
            <span class="btn-title"></span>
          </a-button>
          <a-button class="operation-button" @click="setModal()">
            <span class="iconfont iconfont-16 icon-bianji2"></span>
            <span class="btn-title"></span>
          </a-button>
        </div>
      </div>
      <!--  end -->

      <!--  start -->
      <div class="contents">
        <a-table
          class="default-table"
          :columns="columns"
          :data-source="data"
          :pagination="false"
          :scroll="{ y: 'calc(90vh - 15.7vw)' }"
        >
          <div slot="order">
            <div class="order-box">
              <div class="orderterm" v-for="(item, i) in orderList" :key="i">
                {{ item.name }}
              </div>
            </div>
          </div>
          <div slot="operation" class="operation">
            <div class="count" @click="setModal()">
              <span class="iconfont iconfont-18 icon-bianji"></span>
              <span class="title"></span>
            </div>
          </div>
        </a-table>
      </div>
      <!--  end -->

      <!--  start -->
      <div class="content-pagination">
        <pagination />
      </div>
      <!--  end -->
    </box>
    <mdetails :visible="visibleModal" @close="setModal"></mdetails>
  </div>
</template>

<script>
import Box from "../../components/Box"; //
import Pagination from "../../components/Pagination"; //
import mdetails from "./details"; //
export default {
  components: {
    Box,
    Pagination,
    mdetails
  },
  data() {
    return {
      orderList: [
        {
          id: 1,
          name: ""
        },
        {
          id: 2,
          name: ""
        },
        {
          id: 3,
          name: ""
        },
        {
          id: 4,
          name: ""
        },
        {
          id: 5,
          name: ""
        },
        {
          id: 6,
          name: ""
        }
      ], //
      columns: [
        {
          title: "",
          dataIndex: "sequence",
          key: "sequence",
          width: "3vw"
        },
        {
          title: "",
          dataIndex: "name",
          key: "name",
          width: "5vw"
        },
        {
          title: "",
          dataIndex: "type",
          key: "type",
          width: "5vw"
        },
        {
          title: "",
          dataIndex: "order",
          key: "order",
          width: "36vw",
          scopedSlots: { customRender: "order" }
        },
        {
          title: "",
          dataIndex: "time",
          key: "time",
          width: "10vw"
        },
        {
          title: "",
          dataIndex: "operation",
          key: "operation",
          width: "5vw",
          scopedSlots: { customRender: "operation" }
        }
      ], //
      data: [], //
      selectedRowKeys: [], // 
      visibleModal: false
    };
  },
  mounted() {
    /**
     * 
     */
    for (let i = 0; i < 20; i++) {
      this.data.push({
        key: i,
        sequence: i,
        name: "",
        type: "",
        time: "2021-05-12 10:46:20"
      });
    }
  },
  methods: {
    /**
     * 
     */
    setModal() {
      console.log("===", this.visibleModal);
      this.visibleModal = !this.visibleModal;
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
